@import "mixins";
@import "media";
@import "sprites";
@import "normalize";


$font-weight: 400;
$font-size: 14px;
$line-height: 1.4;
$font-family: "Varela Round";
$font-family2: 'Oswald';
$font-family3: "PT Serif";
$color: #222;
$color-blue: #1f4ba5;
$color-danger: #ff3f2b;


body {
	color: $color;
	font: $font-weight $font-size/$line-height $font-family, Arial, sans-serif;
	background: #FFF; }

/* MAIN STYLES */
/* ================================================ */

.main-wrapper {
	padding: 0;
	min-width: 320px;
	position: relative;
	overflow: hidden;
	opacity: 1;
	min-height: 100%; }



img {
	display: block;
	max-width: 100%; }

span {
	display: inline-block; }

/* text position */

.text-left {
	text-align: left !important; }

.text-center {
	text-align: center !important; }

.text-right {
	text-align: right !important; }


.text-error {
	color: $color-danger; }

/* Icons
     @mixin ===================== */
.ic {
	position: relative;
	display: inline-block;
	vertical-align: middle;
 }	// background: $icons no-repeat

// @each $i in next, prev
// 	.ic--#{$i}
// 		+s(ic--#{$i})


// fonts

// font-family: 'Oswald', sans-serif;
// font-family: 'Varela Round', sans-serif;
// font-family: 'PT Serif', serif;


/* Buttons
     @mixin ===================== */

.btn {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	height: 30px;
	background-color: $color-blue;
	padding: 0 25px;
	border-radius: 20px;
	font-family: $font-family, Arial, sans-serif;
	font-size: 12px;
	font-weight: 400; }

.toggle-menu {
	display: none; }

.favorite {
	color: #b8b8b8;
	font-family: "PT Serif", sans-serif;
	font-size: 12px;
	font-style: italic;
	line-height: 18px;
	.icon {
		font-size: 14px; }
	&:hover {
		color: #ffffff; } }

/* Header
     @mixin ===================== */

.header {
	height: 80px;
	background-color: #fff;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 30px;
	border-bottom: 1px solid #ececec; }

.logo {
	margin-right: 38px;
	padding-bottom: 7px; }

.main-menu {
	margin-right: 70px;
	list-style: none;
	display: flex;

	&__link {
		line-height: 80px;
		color: $color;
		font-size: 14px;
		display: block;
		padding: 0 21px; }
	[class*="angle"] {
		margin-left: 3px;
		font-size: 14px;
		color: $color-blue; } }

.dropdwn {
	position: relative;
	&:hover {
		.dropdown-content {
			opacity: 1;
			margin-top: 0;
			left: -10px;
			visibility: visible; } }
	&--right {
		&:hover {
			.dropdown-content {
				left: auto;
				right: -10px; } }

		.dropdown-content {
			&:before {
				right: 30px;
				z-index: 1; }
			&:after {
				right: 29px; } } }
	&--white {
		.dropdown-content {
			background: #fff;
			border: 1px solid #ececec;
			padding: 0;
			&:before {
				border-bottom-color: #fff; }
			&:after {
				content: '';
				display: block;
				position: absolute;
				bottom: 100%;
				@include arr(16, 9, #ececec, t); } } } }

.dropdown-content {
	position: absolute;
	background: $color;
	padding: 30px;
	width: 240px;
	top: 100%;
	z-index: 99;
	left: -10000px;
	visibility: hidden;
	opacity: 0;
	margin-top: 30px;
	transition: opacity 0.3s, margin-top 0.4s;
	&:before {
		content: '';
		display: block;
		position: absolute;
		bottom: 100%;
		@include arr(14, 7, $color, t); } }

.dropdown-menu {
	list-style: none;
	&__item {
		margin-bottom: 8px;
		&:last-child {
			margin-bottom: 0; } }
	&__link {
		font-size: 12px;
		line-height: 16px;
		color: #bcbcbc;
		&:hover {
			color: #fff; } } }

.search {
	flex: 1;
	position: relative;
	margin-right: 48px;
	&__input {
		width: 100%;
		height: 30px;
		background-color: #f6f6f6;
		padding: 0 40px 0 20px;
		color: $color;
		font-family: 'PT Serif', sans-serif;
		font-size: 12px;
		line-height: 12px;
		border: none;
		border-radius: 20px;
		@include placeholder {
			font-style: italic;
			color: #868686; } }
	&__button {
		position: absolute;
		top: 5px;
		right: 3px;
		font-family: 'FontAwesome'; //For icon search just in case
		background-color: transparent;
		border: none;
		font-size: 18px;


		// пытался сделать иконку поиска
		// input[type="submit"]
 } }  //	// 	font-family: 'FontAwesome' !important

.location {
	margin-right: 47px;
	&__link {
		color: #868686;
		font-size: 12px; }
	[class*="marker"] {
		font-size: 20px;
		color: $color;
		margin-right: 6px; } }


.profile-menu {
	list-style: none;
	display: flex;
	margin-right: -17px;
	&__link {
		display: block;
		line-height: 80px;
		padding: 0 17px; }
	[class*="fa"] {
		font-size: 18px;
		color: $color;
		vertical-align: middle; }
	.dropdown-content {
		width: auto; } }

.basket {
	line-height: $line-height;
	position: relative;
	.badge {
		position: absolute;
		top: -8px;
		right: -9px; } }

.b-price {
	vertical-align: middle;
	margin-left: 13px;
	color: $color;
	font-family: $font-family2;
	font-size: 12px;
	font-weight: 700;
	letter-spacing: 1.2px; }

.badge {
	min-width: 16px;
	height: 16px;
	padding: 0 4px;
	background-color: $color-blue;
	border-radius: 50%;
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	line-height: 17px;
	color: #fff;
	font-size: 9px; }

.user-menu {
	width: 180px;
	list-style: none;
	&__item {
		border-bottom: 1px solid #ececec;
		&:last-child {
			border-bottom: none; } }
	&__link {
		display: flex;
		align-items: center;
		color: $color;
		font-size: 12px;
		font-weight: 400;
		line-height: 40px;
		padding: 0 20px;
		[class*="fa"] {
			width: 18px;
			text-align: center;
			color: #cccccc;
			font-size: 16px;
			margin-right: 15px; }
		.badge {
			margin-left: auto; } } }

.cart {
	width: 300px;
	&-list {
		list-style: none;
		&__item {
			display: flex;
			align-items: center;
			border-bottom: 1px solid #ececec;
			padding: 10px; }
		&__img {
			width: 60px;
			margin-right: 20px;
			flex: none;
			text-align: center;
			img {
				display: inline-block;
				max-width: 100%; } }
		&__info {
			flex: 1;
			.product__title {
				margin-bottom: 10px; } }
		&__details {
			display: flex;
			align-items: center;
			justify-content: space-between;
			.price {
				margin-right: 10px; } }
		&__delete {
			padding: 0 10px 0 0;
			margin-left: 20px;
			flex: none;
			color: #b6b6b6;
			font-size: 12px;
			[class*="fa"] {
				color: #b6b6b6; }
			[class*="fa"]:hover {
				color: $color; } } }
	&-footer {
		display: flex;
		align-items: center;
		padding: 20px 10px;
		.total {
			width: 60px;
			flex: none;
			margin-right: 20px;
			text-align: right; }
		.price {
			margin-right: 28px; } } }

.price {
	color: $color;
	font-size: 12px;
	font-weight: 700;
	letter-spacing: 1.2px;
	font-family: $font-family2, sans-serif;
	&--danger {
		color: $color-danger; } }

.total {
	color: #b8b8b8;
	font-family: $font-family3, sans-serif;
	font-size: 12px;
	font-style: italic;
	line-height: 18px; }

/* Content
     @mixin ===================== */

.header-inner {
	position: relative;
	background-position: right center;
	background-size: cover;
	background-repeat: no-repeat;
	height: 180px;
	padding: 20px 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	text-align: center;
	&__title {
		color: $color;
		font-size: 48px;
		font-weight: 400; }
	.category {
		display: block; }
	.bread-crambs {
		position: absolute;

		left: 30px;
		top: 50%;
		transform: translate(0, -50%); } }

.bread-crambs {
	list-style: none;
	display: flex;
	&__item {
		margin-right: 4px; }
	&__link {
		color: #868686;
		font-size: 12px;
		font-weight: 400;
		&:hover {
			color: $color; } } }

.category {
	color: $color-blue;
	font-family: $font-family2;
	font-size: 12px;
	font-weight: 700;
	text-transform: uppercase;
	letter-spacing: 1.2px; }



.sidebar {
	transition: margin-left 0.4s; }

/* Product
     @mixin ===================== */

.box-product {
	display: flex;
	flex-wrap: wrap;
	margin-right: -1px;
	.product__title {
		height: 30px;
		overflow: hidden; } }

.product {
	position: relative;
	border-right: 1px solid #ececec;
	border-bottom: 1px solid #ececec;
	width: 20%; //14.285%
	padding: 26px 30px;
	text-align: center;
	@include min-screen(992px) {
		&:not(.product--horizontal):hover {
			.product__title, .price {
				color: #fff; }
			.product__footer {
				position: relative;
				z-index: 2; }
			.product__card {
				z-index: 2;
				opacity: 1;
				visibility: visible; }
			&:before {
				height: 100%;
				width: 100%;
				left: 0;
				top: 0; } } }

	&:before {
		content: '';
		display: block;
		z-index: 1;
		position: absolute;
		top: 50%;
		left: 50%;
		height: 0;
		width: 0;
		background: #222;
		opacity: 0.95;
		transition: all 0.4s; }

	&__title {
		color: $color;
		font-size: 12px;
		font-weight: 400;
		line-height: 16px;
		position: relative;
		z-index: 2;
		a {
			color: inherit;
			&:hover {
				text-decoration: underline; } } }

	&__img {
		position: relative;
		margin: 45px 0 60px;
		img {
			margin: 0 auto; } }
	&__card {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		left: -30px;
		right: -30px;
		padding: 0 10px;
		text-align: center;
		top: 50%;
		transform: translate(0, -50%);
		transition: all 0.4s;
		.btn {
			margin-left: 10px; }
		.favorite {
			display: inline-block;
			margin-top: 15px; } }
	&__details {
		color: #b8b8b8;
		font-family: $font-family3, sans-serif;
		font-size: 12px;
		font-style: italic; }

	&__footer {
		display: flex;
		align-items: center;
		justify-content: center;
		.price, .discount, .pbadge {
			width: 33.33%; }
		.discount {
			text-align: right; }
		&.sale {
			.price {
				color: $color-danger; } }
		&.hit, &.new {
			justify-content: flex-start; } }

	.price {
		transition: all 0.4s; }

	&__category {
		color: #b8b8b8;
		font-family: "PT Serif", sans-serif;
		font-size: 12px;
		font-style: italic;
		line-height: 18px; }


	&--horizontal {
		display: flex;
		align-items: center;
		text-align: left;
		width: 100% !important;
		padding: 15px;
		.product__card {
			position: static;
			opacity: 1;
			visibility: visible;
			transform: none;
			text-align: left;
			padding: 0; }
		.product__img {
			margin: 0 44px 0 0;
			flex: none; }

		.product__content {
			flex: 1;
			margin-right: 30px; }

		.product__footer {
			display: block;
			width: 85px;
			margin-right: 20px;

			.price, .discount, .pbadge {
				width: auto; } }

		.product__title {
			height: auto !important;
			margin-bottom: 15px; }

		.price {
			display: block;
			margin-bottom: 20px;
			font-size: 14px; }

		.pbadge, .discount {
			vertical-align: middle; }

		.discount {
			margin-left: 10px; }

		// &:before
		// 	display: none
		// &:hover
		// 	.product__title, .price
 } }		// 		color: $color

.discount {
	color: #a1a1a1;
	font-family: Oswald;
	font-size: 8px;
	font-weight: 700;
	text-decoration: line-through;
	letter-spacing: 0.8px; }

.filter {
	display: flex;
	border-bottom: 1px solid #ececec;
	min-height: 60px;
	&-sidebar {
		display: flex;
		align-items: center;
		padding: 15px 30px;
		width: 240px;
		flex: none;
		border-right: 1px solid #ececec; }
	&-content {
		display: flex;
		align-items: center;
		padding: 15px 30px;
		flex: 1; }
	&-title {
		color: #979797;
		font-size: 12px;
		line-height: 16px; }
	&-list {
		list-style: none;
		margin-left: auto;
		display: flex;
		align-items: center;
		&__item {
			margin-left: 10px;
			&:first-child {
				margin-left: 0; }
			&.active .filter-list__link {
				color: $color; } }
		&__link {
			font-size: 16px;
			color: #d5d5d5;
			&:hover {
				color: #888; } } } }

.toggle-filter {
	color: #d5d5d5;
	font-size: 16px;
	background-color: transparent;
	border: none;
	padding: 0;
	margin-right: 20px;
	transition: all 0.3s;
	&:hover {
		color: $color-blue; }
	.sr-only {
		display: none; } }

.sort {
	display: flex;
	position: relative;
	overflow: hidden;
	input {
		position: absolute;
		left: -999px; }

	input:checked + .sort__item {
		color: #fff;
		background-color: $color;
		padding: 0 20px;
		// [class*="fa-long-arrow"]  // для стрелки которая закомм. в html
		// 	.path1:before
		// 		color: #fff
 }		// 		content: '\f309' //

	&__item {
		display: flex;
		text-align: center;
		justify-content: center;
		align-items: center;
		height: 30px;
		margin-left: 20px;
		font-size: 12px;
		font-weight: 400;
		line-height: 16px;
		border-radius: 20px;
		cursor: pointer;
		&:hover {
			color: $color-blue; }
		[class*="fa"] {
			color: #d5d5d5;
			margin-left: 5px; } } }

.hide {
	position: absolute;
	left: 0;
	top: 0;
	height: 0;
	width: 0;
	overflow: hidden;
	z-index: -1;
	opacity: 0;
	visibility: hidden; }

.tab-cont {
	opacity: 1;
	transition: opacity 0.4s;
	visibility: visible;
	&.hide {
		opacity: 0; } }

.box-main {
	display: flex;
	.sidebar {
		width: 240px;
		flex: none;
		border-right: 1px solid #ececec; }
	.content {
		flex: 1; } }

.accordion {
	border-bottom: 1px solid #ececec;
	&__head {
		height: 70px;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 15px 30px;
		border-top: 1px solid #ececec;
		cursor: pointer;
		&.open {
			[class*="icon"]:before {
				content: '\f067'; } }
		&:first-child {
			border-top: none; }
		[class*="icon"] {
			color: #b8b8b8;
			font-size: 18px;
			margin-left: 10px; } }
	&__title {
		font-family: $font-family2;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 1.4px; }

	&__body {
		padding: 0 30px 25px; } }

.check-list {
	list-style: none;
	&__item {
		margin-bottom: 10px; }
	&__text {
		color: #222222;
		font-size: 12px;
		font-weight: 400;
		line-height: 18px;
		cursor: pointer; }
	&--column {
		.check-list__item {
			display: inline-block;
			vertical-align: top;
			margin-right: -4px;
			width: 50%;
			padding-right: 10px; } } }


.paginator {
	padding: 0 30px;
	height: 60px;
	display: flex;
	align-items: center;
	border-top: 1px solid #ececec;
	margin-top: -1px;
	&__item {
		width: 33.33%;
		color: #979797;
		font-size: 12px;
		font-weight: 400;
		line-height: 18px;
		&:last-child {
			text-align: right; }
		&.load {
			text-align: center;
			color: $color;
			.icon {
				margin-right: 15px;
				font-size: 14px; } } } }



/* Media
     @mixin ===================== */

@include max-screen(1850px) {
	.product {
		width: 16.666%;

		&__card {
			.btn {
				margin-left: 5px; } } } }

@include max-screen(1599px) {
	.product {
		width: 20%; }

	.logo, .main-menu, .search, .location {
		margin-right: 20px; } }

@include max-screen(1399px) {
	.header {
		position: relative; }
	.product {
		width: 25%; }

	.main-menu {
		display: none; }

	.toggle-menu {
		display: block;
		border: none;
		padding: 0;
		background-color: transparent;
		margin-right: 35px;
		font-size: 18px; }

	.logo, .main-menu, .search, .location {
		margin-right: 40px; }

	.dropdown-content {
		&__item {
			padding: 30px;
			width: 25%;
			&:not(:last-child) {
				border-right: 1px solid #353535; } }
		&__title {
			color: #ffffff;
			font-family: Oswald;
			font-size: 14px;
			font-weight: 700;
			text-transform: uppercase;
			letter-spacing: 1.4px;
			margin-bottom: 17px;
			line-height: 30px; }

		&--menu {
			padding: 0;
			display: flex;
			width: 100%;
			&.open {
				opacity: 1;
				margin-top: 0;
				left: 0px;
				visibility: visible; }
			&:before {
				left: 30px; } } }

	.dropdown-menu + .dropdown-content__title {
		margin-top: 45px; }

	.hr {
		height: 1px;
		background-color: #353535;
		margin: 25px 0; } }




@include max-screen(1180px) {
	.product {
		width: 33.33%; } }

@include max-screen(960px) {
	.filter-sidebar {
		width: auto;
		border-right: none; }

	.toggle-filter {
		height: 30px;
		display: flex;
		align-items: center;
		border: 2px solid #d5d5d5;
		border-radius: 20px;
		width: 120px;
		padding: 0 20px;
		&:hover {
			color: #d5d5d5; }
		&.active {
			color: #1f4ba5;
			border-color: #1f4ba5;
			&:hover {
				color: #1f4ba5; } }
		.sr-only {
			display: inline-block;
			font-size: 12px;
			font-weight: 400;
			line-height: 12px;
			font-family: $font-family;
			margin-left: 15px; } }
	.product {
		width: 25%; }

	.box-main {
		position: relative; }
	.content {
		transition: transform 0.4s; }

	.sidebar {
		margin-left: -240px;
		height: 100%;
		overflow-y: auto;
		position: absolute;
		&.open {
			margin-left: 0; }
		&.open + .content {
			transform: translate(240px, 0); } }

	.box-product.full {
		.product {
			width: 33.33%; } } }

@include max-screen(930px) {
	.product {
		width: 33.33%; }
	.box-product.full {
		.product {
			width: 50%; } } }


@include max-screen(768px) {
	.hidden-ipad {
		display: block; } }

@include max-screen(767px) {

	.header {
		padding-left: 20px;
		padding-right: 20px;
		height: 65px; }

	.toggle-menu, .logo, .main-menu, .location {
		margin-right: 25px; }

	.search {
		margin-right: 15px; }

	.location, .hidden-iphone {
		display: none; }

	.dropdown-content--menu {
		display: block; }

	.dropdown-content__item {
		width: auto;
		padding: 0;
		border-bottom: 1px solid #666;
		.hr {
			display: none; } }

	.dropdown-menu {
		display: none;
		padding-left: 30px;
		padding-bottom: 2px; }

	.dropdown-content__title {
		padding: 15px 20px;
		margin-bottom: 0;



		&.d-open + .dropdown-menu {
			display: block; } }

	.dropdown-menu + .dropdown-content__title {
		margin-top: 0; }

	.profile-menu {
		margin-right: -10px; }

	.profile-menu__link {
		line-height: 65px;
		padding: 0 10px; }

	.header-inner {
		padding-left: 20px;
		padding-right: 20px; }

	.header-inner, .bread-crambs {
		left: 2px; }

	.box-product .product__title {
		height: auto; }

	.product {
		width: 50%;
		padding-left: 20px;
		padding-right: 20px;
		&__img {
			margin: 22px 0 35px; }
		&--horizontal {
			.product__img, .product__content, .product__footer {
				margin-right: 20px; } } }

	.box-product.full {
		.product {
			width: 50%; } }


	.filter {
		flex-direction: column;
		&-sidebar {
			padding-left: 20px;
			padding-right: 20px;
			border-bottom: 1px solid #ececec; }
		&-content {
			padding-left: 20px;
			padding-right: 20px;
			.filter__title {
				display: none; } } }
	.sort__item {
		margin-left: 0;
		margin-right: 13px; }

	.paginator {
		padding: 0 20px;
		&__item {
			font-size: 10px;
			&.load .icon {
				margin-right: 5px; } } }

	.header-inner {
		min-height: 150px;
		height: auto;
		padding-top: 40px;
		padding-bottom: 40px;
		.bread-crambs {
			top: 25px;
			transform: none; } }

	.header-inner__title {
		font-size: 28px;
		line-height: 35px; } }

@include max-screen(680px) {
	.product {
		&--horizontal {
			flex-wrap: wrap;

			.product__img {
				margin-bottom: 20px; }

			.product__content {
				min-width: 50%;
				margin-right: 0;
				margin-bottom: 20px; }

			.product__footer, .product__card {
				width: 75%;
				margin-right: 0; }

			.product__footer {
				width: 85px;
				padding-left: 15px; } } } }




@include max-screen(479px) {
	.product,
	.box-product.full .product {
		width: 100%; }

	.b-price, .search__button {
		display: none; }

	.search__input {
		padding: 0 15px; } }
